/* Estilos generales */
body{
  background: #d1defa;
  font-family: sans-serif, Arial, Helvetica;
  text-align: center;
  padding: 0px;
  margin: 0px;
}

.layout{
  width: 100%;
  height: auto;
  display: grid;
  grid-template-areas: 
      "header header header header"
      "nav nav nav nav"
      "content content content lateral"
      "footer footer footer footer"
  ;
  grid-template-rows: 100px 70px auto 70px;
}

/* Estilos cabecera */
.header{
  grid-area: header;
  background: white;
  text-align: left;
  font-style: italic;
  display: flex;
  justify-content: center;
}

.logo{
  background: linear-gradient(90deg, #03228f 10%, #0e73e4 100%);
  width: 40px;
  height: 40px;
  border-radius: 300px;
  margin-top: 30px;
}

.play{
  width: 0;
  height: 0;
  margin-top: 15px;
  margin-left: 16px;
  border-left: 12px solid white;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}

.header h1{
  margin-left: 15px;
  margin-top: 30px;
}

/* Estilos barra de navegación */
.nav{
  grid-area: nav;
  background: linear-gradient(90deg, #03228f 10%, #0e73e4 100%);
}

.nav > ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}

.nav > ul > li {
  padding: 10px;
}

.nav a {
  display: block;
  margin-left: 30px;
  margin-right: 30px;
  color: white;
  text-decoration: none;
  font-size: 18px;
  font-weight: lighter;
  transition: all 300ms;
}

.nav a:hover {
  transform: scale(1.2, 1.2);
}

/* Estilos para el contenido principal */
.content{
  grid-area: content;
  background: #f6f7f9;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.no-film{
  margin-left: 30%;
  margin-top: 10%;
}

.peli-item{
  flex-grow: 1; /* De esta forma cada elemento va a compartir el un espacio equitativo dentro del contenedor */
  background: white;
  border-radius: 10px;
  box-shadow: 0px 0px 6px #ddd;
  margin: 20px;
  padding-top: 60px;
  padding-bottom: 60px;
  padding-left: 25px;
  padding-right: 25px;
}

button,
input[type="submit"],
input[type="button"],
input[type="reset"] {
  width: 100px;
  background: #0b70e1;
  color: white;
  border: 0;
  outline: 0;
  padding: 10px;
  border-radius: 3px;
  margin-top: 15px;
  text-transform: capitalize;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 300ms;
}

.peli-item .edit,
#save {
  margin-right: 7px;
}

.peli-item .edit:hover{
  background: #e77b00;
}

.peli-item .delete:hover{
  background: #9b0202;
}

/* Estilos de la barra lateral */
.lateral{
  grid-area: lateral;
  background: #f6f7f9;
  border-left: 1px solid #ddd;
  padding: 20px;
}

.lateral > div {
  box-sizing: border-box;
  width: 90%;
  margin: 20px;
  padding: 20px;
  padding-top: 10px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 6px #ddd;
}

.lateral input[type="text"],
.lateral input[type="search"],
.lateral textarea,
.edit-form input[type="text"],
.edit-form textarea {
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  padding: 12px;
  border-radius: 5px;
  background: #f6f7f9;
  color: #454545;
  width: 80%;
  /* Con esto podemos quitar el borde interior de los inputs:
  opacity: 4;
  outline: 0;
  border-color: transparent;*/
}

.edit-form input[type="text"],
.edit-form textarea {
  min-width: 300px;
}

.edit-form h3 {
  border-top: 1px solid #ddd;
  padding-top: 15px;
}

.not-searched {
  color: #9b0202;
}

/* Estilos del pie de página */
.footer{
  grid-area: footer;
  background: #d1defa;
  color: #454545;
  border-top: 1pz solid white;
  text-align: left;
  line-height: 68px;
  padding-left: 25px;
}

.footer a {
  color: #0b468a;
  text-decoration: none;
}

/* Vamos a crear un media query para que a partir de esa anchura para abajo, cambien algunas cosas */
@media (max-width: 1085px){
  .content{
      flex-flow: column wrap;
      justify-content: center;
      align-items: center;
  }

  .peli-item{
      width: 80%;
  }
}